.footer {
  overflow: hidden;
  position: relative;
  margin-top: 126px;
  padding-top: 29rem;

  color: @dark-blue;

  --height: 72rem;
  --dynamic-height: 37.5vw;


  &::before {
    position: absolute;
    content: '';
    z-index: 0;
    bottom: 0;
    // top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 101%;

    /* !NB */
    /*       ()   --dynamic-height ( .footer) */
    height: var(--dynamic-height);
    // height: 72rem;
    // height: 37.5vw;

    background-image: url('../img/svg/figures/footer-d.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

@media (min-width: @max-desktop-width) {
  /*
        height  vw
      bottom  1.5   .

    1) --dynamic-height -    ,   vw (720px/72rem = 37.5vw).
    2) --difference-height -       .
    3) --dynamic-bottom -   bottom,     
       (   -   1.5  bottom).

    4)    before   ,    .
    5)   => overflow: hidden =>  .
    6)        padding-top
       (29.2rem)   (--dynamic-padding-top).
  */

  .footer {
    /*    footer */
    --padding-top: 29rem;

    --difference-height: calc(var(--dynamic-height) - var(--height));
    --dynamic-bottom: calc(var(--difference-height) / -1.5);
    --dynamic-padding-top: calc(var(--padding-top) + var(--difference-height) / 3.2);

    padding-top: var(--dynamic-padding-top);

    &::before {
      bottom: var(--dynamic-bottom);
    }
  }
}

.footer__bg-wrap {
  padding-bottom: 11.2rem;
  background-color: #2e2854;
}

