}

.footer__list-column-wrap {
  display: flex;
  justify-content: space-between;
  width: 98.6rem;
}

.footer__list-column {
  max-width: 22rem;
}

.footer__list {}

.footer__item {}

.footer__link {
  display: block;
  margin-bottom: 1.2rem;
